@import url(https://fonts.googleapis.com/css2?family=Barlow:wght@600&display=swap);
body {
  font-family: 'Barlow', sans-serif;
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

#root {
  --window-height: calc(100vh - 2rem);
}

code {
  white-space: pre;
}
.MonitorPanel{height:var(--window-height);display:grid;grid-template-rows:1fr;margin:1rem;position:relative}.MonitorPanel button{position:absolute;left:0.5rem;top:0.5rem}.status.free{background:#93E0FF}.status.busy{background:#C80037}.status.offline{background:#ccc}.room{display:grid;grid-template-columns:1fr 1fr 1fr;grid-template-rows:2fr 1fr;grid-gap:1rem;gap:1rem}.room h1{margin:0;font-size:1rem;color:#fff;position:absolute;right:0.5rem;top:0.5rem}.room:not(.busy) .status{grid-area:1 / 1 / span 2 / span 3}.room.busy .status{grid-area:1 / 1 / span 1 / span 3}.room .aspect{display:grid;grid-template-rows:1fr;grid-template-columns:1fr;justify-items:center;align-items:center;transition:background .25s ease-in-out}.room .aspect svg{width:75%;height:75%;max-width:140px;max-height:140px;transition:fill .25s ease-in-out}.room .aspect.on{background:#C80037}.room .aspect.on svg{fill:#fff}.room .aspect.off{background:#fff}.room .aspect.off svg{fill:#C80037}svg{width:100%;height:100%}.Picker{position:absolute;width:100%;height:100%;background:#fff;display:grid;grid-template-columns:repeat(auto-fit, minmax(13rem, 1fr));grid-gap:1rem;gap:1rem;grid-auto-rows:-webkit-max-content;grid-auto-rows:max-content}.Picker .room{grid-gap:0.75rem;gap:0.75rem}.Picker .slot{padding-top:100%;position:relative}.Picker .slot>div{position:absolute;left:0;right:0;top:0;bottom:0}

